<template>
       <div class="modal" id="modal-answer" tabindex="-1" role="dialog">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title">正解は...</h5>
              <button type="button" class="close" @click="modal_dismiss" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>

            <div class="modal-body">
              <p>{{ train_line_name }}</p>
              <h2>{{ train_station_name }}</h2>
              <h3> {{ result }}</h3>
            </div>

            <div class="modal-footer">
              <button type="button" class="btn btn-primary" v-if="this.$parent.$parent.counter.all < 7" @click="$emit('restart')">次へ進む</button>
              <router-link to="/result" class="btn btn-primary" v-if="this.$parent.$parent.counter.all >= 7">結果発表!</router-link>
              <button type="button" class="btn btn-secondary" v-if="!IsCorrect" @click="modal_dismiss">戻って見てみる</button>

            </div>

          </div>
        </div>
      </div>
</template>
<script>
export default {
   props: ["train_line_name", "train_station_name", "result", "IsCorrect"],
   name:"modal-answer",
   methods:{     
     modal_dismiss(){
       document.getElementById("modal-answer").style.display="none";
     }
   }
}
</script>




