<template>
  <div>
    <div class="container">

      <modal-answer 
        :train_line_name="train_line_name" 
        :train_station_name="train_station_name" 
        :result="result" 
        :IsCorrect="IsCorrect" 
        @restart="restart"></modal-answer>
      <modal-option :options="options" @show_answer="show_answer"></modal-option>

    </div>

    <div id="map"></div>

    <div id="counter">
       <p class="question_num">{{ this.$parent.counter.num }} / 7</p>
    </div>

    <div id="select_button" v-if="this.first" @click="show_options" class="btn btn-lg btn-primary">選ぶ</div>
    <div id="select_button" v-if="!this.first" @click="show_answer" class="btn btn-lg btn-primary">次へ</div>

  </div>
</template>

<style>
  div.container {
    height: inherit;
    padding: 0;
    margin: 0 auto auto;
    text-align: center;
  }

  #map {
    min-width: 80vw;
    min-height: 90vh;
  }

  #select_button{
    position: absolute;
    opacity: 0.8;
    bottom: 9vh;
    right: 4vw;
    z-index:9999;
    width:18vw;
  }
  #counter{
    text-align: right;
    font-size:18pt;
    color:red;
    position: absolute;
    opacity: 0.8;
    bottom: 6vh;
    left: 4vw;
    z-index:9999;

    text-shadow:1px 1px 0 #FFF, -1px -1px 0 #FFF,
              -1px 1px 0 #FFF, 1px -1px 0 #FFF,
              0px 1px 0 #FFF,  0 -1px 0 #FFF,
              -1px 0 0 #FFF, 1px 0 0 #FFF;
  }

</style>

<script>
    import ModalAnswer from '@/components/ModalAnswer'
    import ModalOption from '@/components/ModalOption'

    import  'leaflet/dist/leaflet.css'
    import L from 'leaflet'
    import axios from 'axios';

    
export default {
  components:{
  ModalAnswer,
  ModalOption
  },
    data(){

      return {
        station_data_file:Object,
        train_line_name:"",
        train_station_name:"",
        result:"",
        IsCorrect:false,
        options:Object,
        quiz_data:Object,
        map:Object,
        pin_icon:Object,
        first:true
      }
    },   
    mounted() {
        this.map = L.map('map');

        this.pin_icon = L.icon({
            iconUrl: 'static/train_icon2.png',
            iconSize: [40, 40],
            iconAnchor: [25, 25],
            popupAnchor: [0, -50],
        });

        L.tileLayer('https://cyberjapandata.gsi.go.jp/xyz/seamlessphoto/{z}/{x}/{y}.jpg', {
            attribution: "<a href='https://maps.gsi.go.jp/development/ichiran.html' target='_blank'>地理院タイル</a>|駅データ:<a href='http://www.ekidata.jp' target='_blank'>駅データ.jp</a>"
        }).addTo(this.map);

        this.map.setView([35.6812958, 139.7648991], 14);//とりあえず東京駅が初期値

        this.station_data_file = "./static/station_data/stations_kanto.csv";

        this.$parent.counter.all=0;
        this.$parent.counter.seikai=0;
        this.$parent.counter.num = 1;

        this.init();
        },
    
    methods: {

      init: function(){

        let self = this;

        self.get_station().then((back) => {

          self.quiz_data = back;
          self.options = back.options;

          return self.map_render();

          }).catch(e => {

            console.log("ERROR", e);

          });
      },
      get_station: function () {
        let self = this;

        return new Promise(function (resolve, reject) {

          var options = 4;

          // var req = new XMLHttpRequest(); // HTTPでファイルを読み込むためのXMLHttpRrequestオブジェクトを生成
          // req.open("get", self.station_data_file, true); // アクセスするファイルを指定
          // req.send(null); // HTTPリクエストの発行
          
          // req.onload = function () {
            

          axios.get(self.station_data_file).then((call_back_data) => {
            //読み込まれ次第、ランダムに一要素をとってくる。

            var str = call_back_data.data;

            if(str == ""){
              reject("error at reading csv file",call_back_data);
            }

            var list = str.split("\n");

            var station_options = []; //正解・ダミーを含む選択肢
            var answer_station_data = {}; //正解データだけ

            var random_numbers = self.random_int_array(list.length, options); //[4つの路線番号]
            var answer_num = Math.floor(Math.random() * (options)); //0~3のうち一つの数字

            for (let i = 0; i < random_numbers.length; i++) {

              station_options.push(list[random_numbers[i]]); //選択肢配列に格納

            }

            var station_chosen = list[random_numbers[answer_num]].split(",");

            self.get_line_name(station_chosen[5]).then(line_name => {

              answer_station_data.name = station_chosen[2];
              answer_station_data.lon = station_chosen[9];
              answer_station_data.lat = station_chosen[10];
              answer_station_data.line_id = station_chosen[5];
              answer_station_data.line_pref = station_chosen[6];

              answer_station_data.line_name = line_name;

              resolve({
                options: station_options,
                answer: station_chosen,
                answer_data: answer_station_data
              });

            });
          });
        });
      },
      wiki: function (station_data) {
        return new Promise(function (resolve, reject) {

          let station_name_tmp = station_data.line_name[0];

          var query = {
            "format": "json",
            "action": "query",
            "list": "search",
            "srlimit": 4,
            "srnamespace": 0,
            "srsearch": station_data.name + "駅 " + station_name_tmp.replace(/JR/g, "")
          } //wiki用のクエリを作成

          // console.log(station_data.name + " " + station_name_tmp.replace(/JR/g,""));

          var wiki_end = "http://ja.wikipedia.org/w/api.php";

          axios
          .post(wiki_end, query)
          .done( (data) => {

              let found = false;

              for (i = 0; i < data.query.search.length; i++) {

                if (data.query.search[i].title == station_data.name + "駅") {

                  let pageid = data.query.search[i].pageid;
                  found = true;

                  if (pageid != null) {
                    resolve("https://ja.wikipedia.org?curid=" + pageid);
                  } else {
                    reject(data);
                    console.log("@wiki something wrong", data);
                  }

                }

              }

              if (found == false) {

                resolve(data);
              }

            })
            .fail(function () {
              console.log("ERROR", data);
              reject(data);
            });
        });

      },
      get_line_name: function (line_id) {
        return new Promise(function (resolve, reject) {

            axios
            .get("./static/station_data/line20180424free.csv")
            .then( (data) =>{

            let tmp = data.data.split("\n");
            let end = false;
            let row = [];

            for (let i = 0; i < tmp.length; i++) {

              row = tmp[i].split(',');

              if (row[0] == line_id) {
                resolve([row[2], row[3]]);
                end = true;
                break;
              }
            };
            if (end == false) {
              reject("路線が見つかりませんでした get_line_name()");
            }
          });
        });
      },
      map_render: function () {
        let self = this;

        return new Promise(function (resolve, reject) {

          let station_data = self.quiz_data.answer_data;

          self.map.setView([station_data.lat, station_data.lon], 16);
          L.marker([station_data.lat, station_data.lon],{icon:self.pin_icon}).addTo(self.map);
          self.train_station_name = station_data.name + "駅";
          self.train_line_name = station_data.line_name[0];
          // wiki(station_data).then(url => {
          //     app.station_wiki_url = url;
          // });
          resolve();

        });
      },
      random_int_array: function (max, how_many) {
        var result = [];

        for (var i = 0; i < how_many; i++) {

          let tmp_int_random = Math.floor(Math.random() * (max + 1));

          if (!result.includes(tmp_int_random)) {
            result.push(tmp_int_random);
          }

        }

        return result;
      },
      show_answer: function (station_name) {

        document.getElementById("modal-answer").style.display="block";
        document.getElementById("modal-option").style.display="none";
        //$("#modal-answer").modal('show');

        if(this.first == true){
          this.$parent.counter.all++;

        this.first = false;

        if (station_name == this.quiz_data.answer_data.name) {

          this.result = "正解❗";
          this.IsCorrect = true;
          this.$parent.counter.seikai++;

        } else {
          this.result = "❌不正解❌";
          this.IsCorrect = false;
        }

        }
        


      },
      show_options(event){

        if(this.first=true){
          //$("#modal-option").modal('show');
          document.getElementById("modal-option").style.display="block";

        }

      },
      restart: function (event) {

        //$("#answer_area").modal('hide');
        document.getElementById("modal-option").style.display="none";
        document.getElementById("modal-answer").style.display="none";

        this.first = true;

        this.$parent.counter.num++;

        this.init();

      }
    }
}

</script>



