<template>
        <div class="modal" id="modal-option" tabindex="-1" role="dialog">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title">選択肢</h5>
              <button type="button" class="close" @click="modal_dismiss" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>

            <div class="modal-body">
              <ul class="text-left">
                <li class="btn btn-outline-primary btn-lg" v-for="(option, index) in options" :key="index" v-on:click="$emit('show_answer', option.split(',')[2])">

                  {{ option.split(",")[2] + "駅"}}

                </li>
              </ul>
            </div>

            <div class="modal-footer">
            </div>
          </div>
        </div>
      </div>
</template>
<script>
export default {
   props: ["options"],
   name:"modal-option",
   methods:{
     modal_dismiss(){
       document.getElementById("modal-option").style.display="none";
     }
   }
}
</script>
<style>
  #modal-option .btn {
    margin: auto 8px 8px;
  }
</style>





